.fc {
  background: #FFFFFF;
  background: -webkit-gradient(linear, 0 0, 0 100%, from(#d9eaf3), color-stop(4%, #fff)) 0 4px;
  background: -webkit-linear-gradient(180deg, #d9eaf3 0%, #fff 8%) 0 4px;
  background: -moz-linear-gradient(180deg, #d9eaf3 0%, #fff 8%) 0 4px;
  background: -ms-linear-gradient(180deg, #d9eaf3 0%, #fff 8%) 0 4px;
  background: -o-linear-gradient(180deg, #d9eaf3 0%, #fff 8%) 0 4px;
  background: linear-gradient(180deg, #d9eaf3 0%, #fff 8%) 0 4px;
  background-size: 100% 29px;
  border: 1px solid #DCDFE5;
  padding: 20px 20px 20px 30px;
}
.fc .fc-title {
  margin: -20px -20px 14px -20px;
  background: #FFFFFF;
  padding: 20px 20px 0 20px;
}
.fc p,
.fc li {
  line-height: 1.6;
  margin-bottom: 1.6em;
  margin-top: 0;
}
.fc ol {
  margin: 0;
}
.flash-cards-content {
  position: relative;
  z-index: 2;
}
@media (max-width: 991px) {
  .flash-cards-content .affix {
    position: relative;
  }
}
@media (min-width: 992px) {
  .flash-cards-content .affix {
    width: 271px;
    top: 20px;
    position: fixed;
  }
}
@media (min-width: 1280px) {
  .flash-cards-content .affix {
    width: 270px;
  }
}
@media (min-width: 992px) {
  .flash-cards-content .affix-bottom {
    position: absolute;
    width: 271px;
  }
}
@media (min-width: 1280px) {
  .flash-cards-content .affix-bottom {
    width: 270px;
  }
}
.flash-cards-content .toc {
  font-family: Lato, sans-serif;
  padding-left: 15px;
}
@media (min-width: 992px) {
  .flash-cards-content .toc {
    margin-top: 37px;
  }
}
.flash-cards-content .toc li {
  padding-bottom: .25em;
  margin: 0;
}
.flash-cards-content .toc li.active {
  color: #731454;
}
.flash-cards-content .toc li.active a {
  color: #731454;
}
.flash-cards-content .toc a {
  display: block;
  color: #44474D;
  text-decoration: none;
}
.flash-cards-content .toc a:hover,
.flash-cards-content .toc a:active,
.flash-cards-content .toc a:focus {
  color: #5681B3;
}
.fc-nav {
  width: 100%;
  position: relative;
}
.fc-nav:not(:first-child) {
  padding-top: 15px;
}
.fc-nav .fc-prev,
.fc-nav .fc-next {
  margin: 0;
  cursor: pointer;
  position: relative;
  z-index: 1;
  color: #FFFFFF;
}
.fc-nav .fc-prev {
  float: left;
}
.fc-nav .fc-next {
  float: right;
}
.fc-nav .disabled {
  display: none;
}
.fc-index {
  position: absolute;
  width: 100%;
  padding: 10px;
  margin: 0;
  text-align: center;
  z-index: 0;
  color: #FFFFFF;
  font-family: Lato, sans-serif;
}
.single-flash-cards .above-footer .email-signup-form {
  background: #C3C6CC;
}
.legislator .callout h6,
.legislator .callout .chapters .nav li a,
.chapters .nav li .legislator .callout a,
.legislator .callout .dropdown .widgettitle-in-submenu,
.dropdown .legislator .callout .widgettitle-in-submenu {
  margin: 0;
}
.legislator .callout .h1 {
  margin: 15px 0;
}
.legislator .callout.Democrat {
  background: #314C83;
}
.legislator .callout.Democrat h6,
.legislator .callout.Democrat .chapters .nav li a,
.chapters .nav li .legislator .callout.Democrat a,
.legislator .callout.Democrat .dropdown .widgettitle-in-submenu,
.dropdown .legislator .callout.Democrat .widgettitle-in-submenu,
.legislator .callout.Democrat .h2 {
  color: #FFFFFF;
}
.legislator .callout.Republican {
  background: #9D0E2B;
}
.legislator .callout.Republican h6,
.legislator .callout.Republican .chapters .nav li a,
.chapters .nav li .legislator .callout.Republican a,
.legislator .callout.Republican .dropdown .widgettitle-in-submenu,
.dropdown .legislator .callout.Republican .widgettitle-in-submenu,
.legislator .callout.Republican .h2 {
  color: #FFFFFF;
}
.map-container {
  margin-bottom: 1.88em;
}
.map-mobile {
  margin: 0 auto;
  width: 320px;
}
.map-mobile-scroll {
  width: 100%;
  min-height: .01%;
  overflow-y: hidden;
  overflow-x: auto;
}
.map-tablet {
  margin: 0 auto;
  width: 764px;
}
.map-desktop {
  margin: 0 auto;
  width: 1024px;
}
.map-desktop,
.map-tablet {
  display: none;
}
@media (min-width: 1024px) {
  .map-desktop {
    display: block;
  }
  .map-tablet,
  .map-mobile,
  .map-mobile-scroll {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .map-tablet {
    display: block;
  }
  .map-mobile,
  .map-mobile-scroll {
    display: none;
  }
}
.story-2015-16 .banner .container-fluid,
.story-2015-16 .banner .navbar,
.story-2015-16 .banner:after {
  display: none;
}
.story-2015-16 .brand {
  position: fixed;
}
.story-2015-16 .photo-overlay:after {
  display: none !important;
}
.story-2015-16.admin-bar .brand {
  top: 47px;
}
.story-2015-16 .chapters .nav {
  background: #FFFFFF;
  text-align: center;
}
.mce-content-body {
  margin: 12px !important;
}
.mce-content-body .full-bleed-image .parallax {
  height: 400px;
}
